import React, { useState, useEffect, useContext } from 'react';
import axios from 'axios';
import ProductItem from '../components/productItem';
import Navbar from '../components/navbar';
import SecondaryNavbar from '../components/secondaynavbar';
import { Container, Grid, Slider, Box, Typography } from '@mui/material';
import { useLocation } from 'react-router-dom';
import LeftSidebar from '../components/leftsidebar';
import { AuthContext } from '../AuthContext'; // Importar el contexto de autenticación

const ProductPage = () => {
    const { userId } = useContext(AuthContext); // Obtener el userId del contexto
    const [products, setProducts] = useState([]);
    const [filteredProducts, setFilteredProducts] = useState([]);
    const [priceFilter, setPriceFilter] = useState([0, 500]);
    const [priceRange, setPriceRange] = useState([0, 500]);
    const [subcategories, setSubcategories] = useState([]);
    const [selectedSubcategory, setSelectedSubcategory] = useState(null);
    const [cart, setCart] = useState([]);
    const location = useLocation();
    const params = new URLSearchParams(location.search);
    const category = params.get('category');
    const search = params.get('search');

    useEffect(() => {
        const fetchProducts = async () => {
            try {
                let response;
                if (category && category !== 'all') {
                    response = await axios.get('http://localhost:5000/api/products/', {
                        params: { category }
                    });
                } else {
                    response = await axios.get('http://localhost:5000/api/products/');
                }
                setProducts(response.data);
                updatePriceRange(response.data);
                if (category && category !== 'all') {
                    setSubcategories(getUniqueSubcategories(response.data));
                    setSelectedSubcategory(null); // Reset subcategory
                } else {
                    setSubcategories([]);
                    setSelectedSubcategory(null);
                }
            } catch (error) {
                console.error('Error fetching products:', error);
            }
        };

        fetchProducts();
    }, [category]);

    useEffect(() => {
        filterProducts(products, search, priceFilter, selectedSubcategory);
    }, [products, search, priceFilter, selectedSubcategory]);

    const getUniqueSubcategories = (products) => {
        const subcategories = products.map(product => product.subcategory);
        return [...new Set(subcategories)];
    };

    const updatePriceRange = (products) => {
        const minPrice = Math.min(...products.map(product => product.price));
        const maxPrice = Math.max(...products.map(product => product.price));
        setPriceRange([minPrice, maxPrice]);
        setPriceFilter([minPrice, maxPrice]);
    };

    const filterProducts = (products, search, priceFilter, subcategory = null) => {
        const filtered = products.filter(product => {
            const matchesSearch = product.name.toLowerCase().includes(search?.toLowerCase() || "");
            const matchesPrice = product.price >= priceFilter[0] && product.price <= priceFilter[1];
            const matchesSubcategory = !subcategory || product.subcategory === subcategory;
            return matchesSearch && matchesPrice && matchesSubcategory;
        });
        setFilteredProducts(filtered);
    };

    const updateFilteredProducts = (subcategory) => {
        setSelectedSubcategory(subcategory);
    };

    const handlePriceChange = (event, newValue) => {
        setPriceFilter(newValue);
    };

    const handleAddToCart = async (product) => {
        if (!userId) {
            console.error('User is not logged in');
            return;
        }
        try {
            const requestData = {
                userId: userId,
                productId: product._id,
                quantity: 1
            };
            console.log('Request Data:', requestData);
            
            const response = await axios.post('http://localhost:5000/api/cart/add', requestData);
            
            // Actualizar el carrito localmente después de que se agrega el producto al carrito en el servidor
            setCart([...cart, { ...product, quantity: 1 }]);
        } catch (error) {
            console.error('Error adding product to cart:', error);
        }
    };

    return (
        <div>
            <Navbar />
            <SecondaryNavbar />
            <Container>
            {category !== 'all' && subcategories.length > 0 && (
                <Box sx={{ textAlign: 'center', marginBottom: '10px' }}>
                    <Typography variant='h5' sx={{ marginTop: '15px'}}>Rango de precio</Typography>
                    <Slider
                        value={priceFilter}
                        onChange={handlePriceChange}
                        valueLabelDisplay="auto"
                        min={priceRange[0]}
                        max={priceRange[1]}
                        step={2}
                        marks={[
                            { value: priceRange[0], label: `$${priceRange[0]}` },
                            { value: priceRange[1], label: `$${priceRange[1]}` }
                        ]}
                        sx={{
                            margin: '10px',
                            fontSize: '12px',
                            height: '4px',
                            width: '25rem',
                            '@media (max-width: 600px)': {
                                width: '15rem',
                            },
                        }}
                    />
                </Box>
                                )}
                {category !== 'all' && subcategories.length > 0 && (
                    <Grid>
                        <Grid item xs={12} sm={8} md={9} lg={10}>
                            <Box sx={{ marginBottom: '20px' }}>
                                <Grid container spacing={2}>
                                    {subcategories.map((subcategory, index) => (
                                        <Grid item xs={6} key={index}>
                                            <Typography
                                                variant="body1"
                                                onClick={() => updateFilteredProducts(subcategory)}
                                                sx={{
                                                    cursor: 'pointer',
                                                    marginBottom: '8px',
                                                    padding: '8px',
                                                    border: '1px solid #ccc',
                                                    borderRadius: '4px',
                                                    backgroundColor: '#f0f0f0',
                                                    transition: 'transform 0.2s ease-in-out',
                                                    '&:hover': {
                                                        transform: 'scale(1.1)',
                                                    },
                                                }}
                                            >
                                                {subcategory}
                                            </Typography>
                                        </Grid>
                                    ))}
                                </Grid>
                            </Box>
                        </Grid>
                    </Grid>
                )}
                <Grid container spacing={3}>
                    {filteredProducts.map(product => (
                        <Grid item xs={12} sm={6} md={4} lg={3} key={product._id}>
                            <ProductItem
                                name={product.name}
                                price={product.price}
                                category={product.category}
                                subcategory={product.subcategory}
                                imageUrl={product.imageUrl}
                                onAddToCart={() => handleAddToCart(product)}
                                sx={{ width: '100%', height: '100%' }}
                            />
                        </Grid>
                    ))}
                </Grid>
            </Container>
        </div>
    );
};

export default ProductPage;
