import React from 'react';

const Carousel = () => {
  return (
    <div style={{ maxWidth: '100%', marginTop: '100px', overflowX: 'auto' }}>
      <div style={{ display: 'flex' }}>
        <img src="/img/b1.jpg" alt="b1" style={imageStyle} />
        <img src="/img/b2.jpg" alt="b2" style={imageStyle} />
        <img src="/img/b3.jpg" alt="b3" style={imageStyle} />
        <img src="/img/b4.jpg" alt="b4" style={imageStyle} />
        <img src="/img/b5.jpg" alt="b5" style={imageStyle} />
        <img src="/img/b6.jpg" alt="b6" style={imageStyle} />
        <img src="/img/b7.jpg" alt="b7" style={imageStyle} />
        <img src="/img/b8.jpg" alt="b8" style={imageStyle} />
        <img src="/img/b9.jpg" alt="b9" style={imageStyle} />
        <img src="/img/b10.jpg" alt="b10" style={imageStyle} />
      </div>
    </div>
  );
};

const imageStyle = {
  width: '200px',
  height: '200px',
  borderRadius: '50%', // Hace las imágenes circulares
  marginRight: '22px', // Espacio entre las imágenes
};

export default Carousel;
