import React from 'react';
import { Card, CardContent, CardMedia, Typography, Button } from '@mui/material';

const ProductItem = ({ name, description, price, category, subcategory, imageUrl, onAddToCart, ...props }) => {
    return (
        <Card {...props}>
            <CardMedia
                component="img"
                alt={name}
                height="200" // Altura deseada de la imagen
                width="100%" // Ancho completo del contenedor
                image={imageUrl}
                title={name}
                sx={{
                    margin: 'auto', // Ajusta el margen automático para centrar la imagen horizontalmente
                    mt: 1, // Ajusta el margen superior para agregar espacio entre la imagen y el contenido
                }}
            />
            <CardContent>
                <Typography gutterBottom variant="h6" component="div">
                    {name}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                    {description}
                </Typography>
                <Typography variant="subtitle1" color="text.primary">
                    {price}€
                </Typography>
                <Button
                    variant="contained"
                    color="primary"
                    size="small"
                    sx={{ mt: 1, backgroundColor: '#D63F1F' }} // Establece el color de fondo del botón
                    onClick={onAddToCart} // Añade el evento onClick
                >
                    Añadir
                </Button>
            </CardContent>
        </Card>
    );
};

export default ProductItem;
