import React, { createContext, useState, useEffect } from 'react';
import axios from 'axios';
import { jwtDecode } from 'jwt-decode';

export const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const initialToken = localStorage.getItem('token');
  const initialAuthProvider = localStorage.getItem('authProvider');
  const initialUserId = localStorage.getItem('userId'); // Obtener userId de localStorage

  const [auth, setAuth] = useState(initialToken);
  const [userId, setUserId] = useState(initialUserId); // Inicializar userId desde localStorage
  const [authProvider, setAuthProvider] = useState(initialAuthProvider);
  const [notifications, setNotifications] = useState([]);
  const [cart, setCart] = useState([]);

  useEffect(() => {
    // Cargar datos adicionales como el carrito y las notificaciones desde localStorage
    const savedCart = JSON.parse(localStorage.getItem('cart')) || [];
    setCart(savedCart);

    const savedNotifications = JSON.parse(localStorage.getItem('notifications')) || [];
    setNotifications(savedNotifications);
  }, []);

  const login = (token, provider) => {
    localStorage.setItem('token', token);
    localStorage.setItem('authProvider', provider);
    try {
      const decodedToken = jwtDecode(token);
      const userId = decodedToken.id;
      setAuth(token);
      setUserId(userId);
      localStorage.setItem('userId', userId); // Guardar userId en localStorage
      setAuthProvider(provider);
    } catch (error) {
      console.error("Invalid token:", error);
    }
  };

  const logout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('authProvider');
    localStorage.removeItem('userId'); // Eliminar userId de localStorage
    localStorage.removeItem('notifications');
    localStorage.removeItem('cart');
    setAuth(null);
    setUserId(null);
    setAuthProvider(null);
    setNotifications([]);
    setCart([]);
  };

  const addNotification = (notification) => {
    const updatedNotifications = [...notifications, notification];
    setNotifications(updatedNotifications);
    localStorage.setItem('notifications', JSON.stringify(updatedNotifications));
  };

  const handleAddToCart = async (product) => {
    if (!userId) {
      console.error('User is not logged in');
      return;
    }
    try {
      const requestData = {
        userId: userId,
        productId: product._id,
        quantity: 1
      };
      console.log('Request Data:', requestData);

      const response = await axios.post('http://localhost:5000/api/cart/add', requestData);

      // Actualizar el carrito localmente después de que se agrega el producto al carrito en el servidor
      setCart([...cart, { ...product, quantity: 1 }]);
    } catch (error) {
      console.error('Error adding product to cart:', error);
    }
  };

  return (
    <AuthContext.Provider value={{ auth, userId, login, logout, authProvider, notifications, handleAddToCart }}>
      {children}
    </AuthContext.Provider>
  );
};
