import React, { useState, useEffect, useContext } from 'react';
import axios from 'axios';
import { AuthContext } from '../AuthContext';
import {jwtDecode} from 'jwt-decode';
import { Typography, Container, Grid, Button, Card, CardContent, CardMedia, Box } from '@mui/material';
import Navbar from '../components/navbar';
import SecondaryNavbar from '../components/secondaynavbar';

const Cart = () => {
    const { auth } = useContext(AuthContext);
    const [cart, setCart] = useState([]);

    useEffect(() => {
        const fetchCart = async () => {
            if (!auth) {
                console.error('User is not logged in');
                return;
            }

            const decodedToken = jwtDecode(auth);
            const userId = decodedToken.id;

            try {
                const response = await axios.get(`http://localhost:5000/api/cart/${userId}`);
                setCart(response.data.products);
            } catch (error) {
                console.error('Error fetching cart:', error);
            }
        };

        fetchCart();
    }, [auth]);

    const removeFromCart = async (productId) => {
        if (!auth) {
            console.error('User is not logged in');
            return;
        }

        const decodedToken = jwtDecode(auth);
        const userId = decodedToken.id;

        try {
            const response = await axios.delete('http://localhost:5000/api/cart/remove', {
                data: { userId, productId }
            });

            if (response.status === 200) {
                setCart(response.data.cart.products);
                alert('Product removed from cart');
            }
        } catch (error) {
            console.error('Error removing product from cart:', error);
            alert('Error removing product from cart');
        }
    };

    const subtotal = cart.reduce((total, product) => total + (product.productId.price * product.quantity), 0);
    const totalProducts = cart.reduce((total, product) => total + product.quantity, 0);

    return (
        <>
            <Navbar />
            <SecondaryNavbar />
            <Container sx={{ marginTop: '50px' }}>
                <Typography variant="h4" gutterBottom sx={{ marginBottom: '30px', paddingBottom: '30px', borderBottom: '1px solid gray', fontWeight: '500' }}>
                    Tus Productos
                </Typography>
                <Grid container spacing={2}>
                    <Grid item xs={12} md={8}>
                        {cart.length === 0 ? (
                            <Typography variant="body1">
                                No hay productos en el carrito.
                            </Typography>
                        ) : (
                            <Grid container spacing={2}>
                                {cart.map((product, index) => (
                                    <Grid item xs={12} key={index}>
                                        <Card sx={{ display: 'flex', height: '100%' }}>
                                            <CardMedia
                                                component="img"
                                                height="100%"
                                                image={product.productId.imageUrl}
                                                alt={product.productId.name}
                                                sx={{ width: '30%' }} // Ajusta el ancho de la imagen
                                            />
                                            <CardContent sx={{ flex: 1 }}>
                                                <Typography variant="h6">{product.productId.name}</Typography>
                                                <Typography variant="body2">{product.productId.description}</Typography>
                                                <Typography variant="subtitle1">Precio: ${product.productId.price}</Typography>
                                                <Typography variant="subtitle1">Cantidad: {product.quantity}</Typography>
                                                <Button variant="outlined" onClick={() => removeFromCart(product.productId._id)}>
                                                    Quitar del carrito
                                                </Button>
                                            </CardContent>
                                        </Card>
                                    </Grid>
                                ))}
                            </Grid>
                        )}
                    </Grid>
                    <Grid item xs={12} md={4}>
                        <Box sx={{ padding: '20px', borderLeft: '1px solid #ccc' }}>
                            <Typography variant="subtitle1" gutterBottom>
                                Subtotal ({totalProducts} productos): ${subtotal.toFixed(2)}
                            </Typography>
                            <Button variant="contained" color="primary">
                                Tramitar pedido
                            </Button>
                        </Box>
                    </Grid>
                </Grid>
            </Container>
        </>
    );
};

export default Cart;
