// Importa los módulos necesarios
import React, { useContext } from 'react';
import { Navigate } from 'react-router-dom';
import { AuthContext } from './AuthContext';

// Define el componente PrivateRoute
const PrivateRoute = ({ element }) => {
  // Obtiene el contexto de autenticación
  const { user } = useContext(AuthContext);

  // Obtiene el token del localStorage
  const token = localStorage.getItem('token');

  // Verifica si hay un usuario autenticado o un token presente en el localStorage
  if (user || token) {
    // Si hay un usuario autenticado o un token presente, renderiza el elemento recibido como prop
    return element;
  } else {
    // Si no hay un usuario autenticado ni un token presente, redirige al usuario a la página de inicio de sesión
    return <Navigate to="/login" />;
  }
};

// Exporta el componente PrivateRoute
export default PrivateRoute;
