import React, { useContext } from 'react';
import { Button, Card, CardContent, CardMedia, Grid, Link, Typography } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import Navbar from '../components/navbar';
import Carousel from './Carousel';
import { useMediaQuery } from '@mui/material';
import { AuthContext } from '../AuthContext';
import SecondaryNavbar from '../components/secondaynavbar';


const HomePage = () => {
  const { auth } = useContext(AuthContext);
  const navigate = useNavigate();

  const images = {
    home1: '/img/home1.jpg',
    home2: '/img/home2.jpg',
    home3: '/img/home3.jpg',
    home4: '/img/home4.jpg',
    home5: '/img/home5.jpg',
    home6: '/img/home6.jpg',
    home7: '/img/home7.jpg',
    home8: '/img/home8.jpg',
    home9: '/img/home9.jpg',
    home10: '/img/home10.jpg',
    home11: '/img/home11.jpg',
    home12: '/img/home12.jpg',
    home13: '/img/home13.jpg',
    fondo: '/img/fondo.jpg'
  };

  const isMdScreen = useMediaQuery((theme) => theme.breakpoints.up('md'));

  return (
    <div>
      <Navbar />
      <SecondaryNavbar />
      <div style={{ width: '100%', height: '100vh', display: 'flex', justifyContent: 'center' }}>
        <div
          style={{
            width: isMdScreen ? '60%' : '100%',
            padding: '20px',
            backgroundImage: isMdScreen ? `url(${images.fondo})` : 'none',
            backgroundRepeat: 'no-repeat',
            backgroundSize: 'contain',
          }}          
        >
          <div style={{ marginTop: isMdScreen ? '200px' : '0px' }}>
            <Grid container spacing={3}>
              <Grid item xs={12} sm={6} md={3}>
                <Card>
                  <CardContent>
                    <Typography gutterBottom variant="h6" component="div">
                      Ofertas Top
                    </Typography>
                    <Grid container spacing={1}>
                      <Grid item xs={6}>
                        <img src={images.home1} alt="Oferta 1" style={{ width: '100%' }} />
                      </Grid>
                      <Grid item xs={6}>
                        <img src={images.home13} alt="Oferta 2" style={{ width: '100%' }} />
                      </Grid>
                      <Grid item xs={6}>
                        <img src={images.home3} alt="Oferta 3" style={{ width: '100%' }} />
                      </Grid>
                      <Grid item xs={6}>
                        <img src={images.home4} alt="Oferta 4" style={{ width: '100%' }} />
                      </Grid>
                    </Grid>
                  </CardContent>
                </Card>
              </Grid>

              <Grid item xs={12} sm={6} md={3}>
                <Card>
                  <CardContent>
                    <Typography gutterBottom variant="h6" component="div">
                      Productos usados
                    </Typography>
                    <Grid container spacing={1}>
                      <Grid item xs={6}>
                        <img src={images.home5} alt="Oferta 5" style={{ width: '100%' }} />
                      </Grid>
                      <Grid item xs={6}>
                        <img src={images.home6} alt="Oferta 6" style={{ width: '100%' }} />
                      </Grid>
                      <Grid item xs={6}>
                        <img src={images.home7} alt="Oferta 7" style={{ width: '100%' }} />
                      </Grid>
                      <Grid item xs={6}>
                        <img src={images.home8} alt="Oferta 8" style={{ width: '100%' }} />
                      </Grid>
                    </Grid>
                    <Typography variant="body2" color="text.secondary" align="center" marginTop={"40px"}>
                      <Link to="#" style={{ textDecoration: 'none', color: 'black', fontSize: '18px' }}>
                        Ver más
                      </Link>
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>

              <Grid item xs={12} sm={6} md={3}>
                <Card>
                  <CardContent>
                    <Typography gutterBottom variant="h6" component="div">
                      Ofertas Flash
                    </Typography>
                  </CardContent>
                  <Grid container justifyContent="center" alignItems="center" style={{ padding: '10px' }}>
                    <Grid item xs={12}>
                      <CardMedia
                        component="img"
                        image={images.home9}
                        alt="Ofertas Flash"
                        style={{ margin: 'auto', display: 'block' }}
                      />
                    </Grid>
                  </Grid>
                </Card>
              </Grid>

              {!auth && (
                <Grid item xs={12} sm={6} md={3}>
                  <Card>
                    <CardContent>
                      <Typography gutterBottom variant="h6" component="div">
                        Identifícate para una mejor experiencia
                      </Typography>
                      <Button
                        variant="contained"
                        color="primary"
                        onClick={() => navigate('/register')}
                        style={{ marginTop: '16px' }}
                      >
                        Registrarse
                      </Button>
                    </CardContent>
                  </Card>
                </Grid>
              )}

              {auth && (
                <Grid item xs={12} sm={6} md={3}>
                  <Card>
                    <CardContent>
                      <Typography gutterBottom variant="h6" component="div">
                        Ofertas Flash
                      </Typography>
                    </CardContent>
                    <Grid container justifyContent="center" alignItems="center" style={{ padding: '10px' }}>
                      <Grid item xs={12}>
                        <CardMedia
                          component="img"
                          image={images.home10}
                          alt="Ofertas Flash"
                          style={{ margin: 'auto', display: 'block' }}
                        />
                      </Grid>
                    </Grid>
                  </Card>
                </Grid>
              )}

              {[
                { imageUrl: images.home10, name: 'Electrodomésticos', category: 'Electrodomésticos' },
                { imageUrl: images.home11, name: 'Deporte', category: 'Deporte' },
                { imageUrl: images.home12, name: 'Cosméticos', category: 'Cosméticos' },
                { imageUrl: images.home2, name: 'Apoya la roja en la Euro', category: 'Tecnología' },
              ].map((image, index) => (
                <Grid item xs={12} sm={6} md={3} key={index}>
                  <Card onClick={() => navigate(`/productos?category=${image.category}`)} style={{ cursor: 'pointer' }}>
                    <CardContent>
                      <Typography gutterBottom variant="h6" component="div">
                        {image.name}
                      </Typography>
                      <CardMedia
                        component="img"
                        height="200"
                        image={image.imageUrl}
                        alt={image.name}
                      />
                    </CardContent>
                  </Card>
                </Grid>
              ))}

            </Grid>
            <Carousel />
          </div>
        </div>
      </div>
    </div>
  );
};

export default HomePage;
