import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { AuthProvider } from './AuthContext';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import RegisterPage from './pages/RegisterPage';
import LoginPage from './pages/LoginPages';
import HomePage from './pages/HomePage';
import MiCuenta from './pages/Micuenta';
import InfoUser from './pages/info-user';
import PrivateRoute from './PrivateRoute';
import ProductPage from './pages/ProductPage';
import SearchPage from './pages/searchPage'; // Importa la página SearchPage
import Carrito from './pages/Carrito';
import NotFoundPage from './pages/NotFoundPage'; // Importa la página NotFoundPage
import SendEmail from './pages/forgotpassword';
import ForgotPassword from './pages/forgotpassword';
import ResetPassword from './pages/resetpassword';


const theme = createTheme({
  breakpoints: {
    values: {
      xs: 0,
      sm: 400,
      md: 960,
      lg: 1280,
      xl: 1920,
    },
  },
});

function App() {
  return (
    <ThemeProvider theme={theme}>
      <AuthProvider>
        <Router>
          <Routes>
            {/* Ruta para el registro */}
            <Route path="/register" element={<RegisterPage />} />
            
            {/* Ruta para el inicio de sesión */}
            <Route path="/login" element={<LoginPage />} />

            {/* Ruta para la página principal */}
            <Route path="/" element={<HomePage />} />

            <Route path="/forgotpassword" element={<ForgotPassword />} />

            <Route path="/resetpassword/:token" element={<ResetPassword />} />

            <Route path="/sendemail" element={<SendEmail />} />

            {/* Ruta para la página de productos */}
            <Route path="/productos" element={<ProductPage />} />

            {/* Ruta para la búsqueda */}
            <Route path="/search" element={<SearchPage />} />

            {/* Rutas protegidas */}
            <Route path="/miCuenta" element={<PrivateRoute element={<MiCuenta />} />} />
            <Route path="/carrito" element={<PrivateRoute element={<Carrito />} />} />
            <Route path="/info-user" element={<PrivateRoute element={<InfoUser />} />} />

            {/* Ruta para manejar 404 */}
            <Route path="*" element={<NotFoundPage />} />
          </Routes>
        </Router>
      </AuthProvider>
    </ThemeProvider>
  );
}

export default App;
