import React, { useState } from 'react';
import { Card, CardContent, Typography, Grid } from '@mui/material';
import { useNavigate } from 'react-router-dom'; // Importa useNavigate para la redirección
import Navbar from '../components/navbar';
import SecondaryNavbar from '../components/secondaynavbar';
import { useMediaQuery } from '@mui/material';

const MiCuenta = () => {
    const [selectedSection, setSelectedSection] = useState('user-info');
    const navigate = useNavigate(); // Inicializa navigate para la redirección

    const handleSectionChange = (section) => {
        if (section === 'user-info') {
            navigate('/info-user'); // Redirige a la página de información del usuario
        }
    };

    const images = {
        info: '/img/info.png',
        pedidos: '/img/pedidos.png',
        direcciones: '/img/direcciones.png',
        empresa: '/img/empresa.jpg',
        pagos: '/img/pagos.png',
        chequesRegalo: '/img/cheques-regalos.png',
        mensajeria: '/img/mensajeria.png',
        serviciosDigitales: '/img/servicios-digitales.png',
        contacto: '/img/contacto.png',
        listas: '/img/listas.png',
        appAmazon: '/img/app-amazon.png',
        prime: '/img/prime.png',
    };

    const isMdScreen = useMediaQuery((theme) => theme.breakpoints.up('md'));

    return (
        <div>
            <Navbar />
            <SecondaryNavbar />
            <div style={{ width: '100%', height: '100vh', display: 'flex', justifyContent: 'center', marginTop: isMdScreen ? '50px' : '15px'}}>
                <div style={{ width: isMdScreen ? '60%' : '98%', }}>
                    <h1 style={{ textAlign: 'center' }}>Mi Cuenta</h1>
                    <Grid container spacing={3} style={{marginTop: isMdScreen ? '40px' : '0px', justifyContent: "center"}}>
                        <Grid item xs={12} md={4}>
                            <Card onClick={() => handleSectionChange('user-info')} style={{ cursor: 'pointer', display: 'flex' }}>
                                <img src={images.info} alt="User Info" style={{ width: '100px', marginRight: '20px' }} />
                                <CardContent>
                                    <Typography variant="h5" component="h2" align="right">
                                        Información de Usuario
                                    </Typography>
                                    <Typography variant="body1" align="right">
                                        Aquí puedes ver y editar tu información personal.
                                    </Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={12} md={4}>
                            <Card onClick={() => handleSectionChange('purchase-history')} style={{ cursor: 'pointer', display: 'flex' }}>
                                <img src={images.pedidos} alt="Purchase History" style={{ width: '100px', marginRight: '20px' }} />
                                <CardContent>
                                    <Typography variant="h5" component="h2" align="right">
                                        Historial de Compras
                                    </Typography>
                                    <Typography variant="body1" align="right">
                                        Revisa tus compras anteriores.
                                    </Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={12} md={4}>
                            <Card onClick={() => handleSectionChange('prime')} style={{ cursor: 'pointer', display: 'flex' }}>
                                <img src={images.prime} alt="Prime" style={{ width: '100px', marginRight: '20px' }} />
                                <CardContent>
                                    <Typography variant="h5" component="h2" align="right">
                                        Prime
                                    </Typography>
                                    <Typography variant="body1" align="right">
                                        Administrar tu suscripción, consultar las ventajas y la configuración de pago
                                    </Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={12} md={4}>
                            <Card onClick={() => handleSectionChange('addresses')} style={{ cursor: 'pointer', display: 'flex' }}>
                                <img src={images.direcciones} alt="Addresses" style={{ width: '100px', marginRight: '20px' }} />
                                <CardContent>
                                    <Typography variant="h5" component="h2" align="right">
                                        Direcciones
                                    </Typography>
                                    <Typography variant="body1" align="right">
                                        Editar, eliminar o configurar la dirección predeterminada.
                                    </Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={12} md={4}>
                            <Card onClick={() => handleSectionChange('company-account')} style={{ cursor: 'pointer', display: 'flex' }}>
                                <img src={images.empresa} alt="Company Account" style={{ width: '100px', height: '80', marginRight: '20px' }} />
                                <CardContent>
                                    <Typography variant="h5" component="h2" align="right">
                                        Tu cuenta de empresa
                                    </Typography>
                                    <Typography variant="body1" align="right">
                                        Regístrate para descargar fácilmente las facturas con IVA y separar tus compras de empresa.
                                    </Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={12} md={4}>
                            <Card onClick={() => handleSectionChange('payments')} style={{ cursor: 'pointer', display: 'flex' }}>
                                <img src={images.pagos} alt="Payments" style={{ width: '100px', marginRight: '20px' }} />
                                <CardContent>
                                    <Typography variant="h5" component="h2" align="right">
                                        Mis pagos
                                    </Typography>
                                    <Typography variant="body1" align="right">
                                        Administrar o añadir métodos de pago y ver tus transacciones.
                                    </Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={12} md={4}>
                            <Card onClick={() => handleSectionChange('gift-cards')} style={{ cursor: 'pointer', display: 'flex' }}>
                                <img src={images.chequesRegalo} alt="Gift Cards" style={{ width: '100px', marginRight: '20px' }} />
                                <CardContent>
                                    <Typography variant="h5" component="h2" align="right">
                                        Cheques regalo y Recargar cuenta
                                    </Typography>
                                    <Typography variant="body1" align="right">
                                        Ver el saldo o canjear una tarjeta y comprar nuevas tarjetas regalo.
                                    </Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={12} md={4}>
                            <Card onClick={() => handleSectionChange('messaging-service')} style={{ cursor: 'pointer', display: 'flex' }}>
                                <img src={images.mensajeria} alt="Messaging Service" style={{ width: '100px', marginRight: '20px' }} />
                                <CardContent>
                                    <Typography variant="h5" component="h2" align="right">
                                        Servicio de mensajería
                                    </Typography>
                                    <Typography variant="body1" align="right">
                                        Ver o responder los mensajes de vendedores y compradores de Amazon.
                                    </Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={12} md={4}>
                            <Card onClick={() => handleSectionChange('digital-services')} style={{ cursor: 'pointer', display: 'flex' }}>
                                <img src={images.serviciosDigitales} alt="Digital Services" style={{ width: '100px', marginRight: '20px' }} />
                                <CardContent>
                                    <Typography variant="h5" component="h2" align="right">
                                        Servicios digitales y soporte para dispositivos
                                    </Typography>
                                    <Typography variant="body1" align="right">
                                        Solucionar problemas del dispositivo, gestionar o cancelar suscripciones digitales.
                                    </Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={12} md={4}>
                            <Card onClick={() => handleSectionChange('contact-us')} style={{ cursor: 'pointer', display: 'flex' }}>
                                <img src={images.contacto} alt="Contact Us" style={{ width: '100px', marginRight: '20px' }} />
                                <CardContent>
                                    <Typography variant="h5" component="h2" align="right">
                                        Contáctanos
                                    </Typography>
                                    <Typography variant="body1" align="right">
                                        Explorar las opciones de autoservicio, los artículos de ayuda o contactarnos.
                                    </Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={12} md={4}>
                            <Card onClick={() => handleSectionChange('my-lists')} style={{ cursor: 'pointer', display: 'flex' }}>
                                <img src={images.listas} alt="My Lists" style={{ width: '100px', marginRight: '20px' }} />
                                <CardContent>
                                    <Typography variant="h5" component="h2" align="right">
                                        Mis listas
                                    </Typography>
                                    <Typography variant="body1" align="right">
                                        Ve, modifica y comparte tus listas o crea otras nuevas.
                                    </Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={12} md={4}>
                            <Card onClick={() => handleSectionChange('amazon-app')} style={{ cursor: 'pointer', display: 'flex' }}>
                                <img src={images.appAmazon} alt="Amazon App" style={{ width: '100px', marginRight: '20px' }} />
                                <CardContent>
                                    <Typography variant="h5" component="h2" align="right">
                                        App Amazon
                                    </Typography>
                                    <Typography variant="body1" align="right">
                                        Descargar la aplicación de Amazon con estos estilos.
                                    </Typography>
                                </CardContent>
                            </Card>
                        </Grid>
                    </Grid>
                </div>
            </div>
        </div>
    );
};

export default MiCuenta;
