import React, { useState, useContext } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { Container, TextField, Button, Typography, Box, Divider, IconButton, InputAdornment, Paper } from '@mui/material';
import { Visibility, VisibilityOff } from '@mui/icons-material';
import { AuthContext } from '../AuthContext';
import { GoogleAuthProvider, signInWithPopup } from 'firebase/auth';
import { auth } from '../firebase/firebase';
import { jwtDecode } from 'jwt-decode'; // Importación corregida de jwtDecode

const provider = new GoogleAuthProvider();

const LoginPage = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [message, setMessage] = useState('');
  const { login } = useContext(AuthContext);
  const navigate = useNavigate();

  // Función para manejar el inicio de sesión con Google
  const handleGoogleLogin = async () => {
    try {
      const result = await signInWithPopup(auth, provider);
      const idToken = await result.user.getIdToken();

      const response = await fetch('http://localhost:5000/api/auth/google-login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ idToken }),
      });

      const data = await response.json();

      if (response.ok) {
        const decodedToken = jwtDecode(data.token);
        login(data.token, decodedToken.authProvider);
        navigate('/'); // Redirige al home
      } else {
        setMessage('Error: ' + data.message);
      }
    } catch (error) {
      console.error('Error al iniciar sesión con Google:', error.message);
      setMessage('Error al iniciar sesión con Google.');
    }
  };

  // Función para manejar el inicio de sesión con email y contraseña
  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const response = await fetch('http://localhost:5000/api/auth/login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ email, password }),
      });
      const data = await response.json();

      if (response.ok) {
        login(data.token);
        navigate('/'); // Redirige al usuario a la página de inicio (o donde desees)
      } else {
        setMessage('Error: ' + data.message);
      }
    } catch (error) {
      console.error('Error:', error);
      setMessage('Error al iniciar sesión.');
    }
  };

  return (
    <Container
      maxWidth="xs"
      sx={{
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        height: '100vh',
      }}
    >
      <Typography variant="h4" align="center" gutterBottom sx={{ fontFamily: 'emoji' }}>
        Aguizon
      </Typography>
      <Paper elevation={3} sx={{ padding: '20px', textAlign: 'center', width: '100%' }}>
        <Typography variant="h5" gutterBottom>
          Iniciar Sesión
        </Typography>
        <form onSubmit={handleSubmit}>
          <TextField
            name="email"
            label="Dirección de e-mail o número de teléfono móvil"
            variant="outlined"
            fullWidth
            margin="normal"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          <TextField
            name="password"
            label="Contraseña"
            type={showPassword ? 'text' : 'password'}
            variant="outlined"
            fullWidth
            margin="normal"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton onClick={() => setShowPassword(!showPassword)} edge="end">
                    {showPassword ? <VisibilityOff /> : <Visibility />}
                  </IconButton>
                </InputAdornment>
              ),
            }}
          />
          <Button
            type="submit"
            variant="contained"
            fullWidth
            sx={{
              backgroundColor: '#EA4335',
              mt: 2,
              mb: 2,
              '&:hover': {
                backgroundColor: '#CC3127', // Color más oscuro para el hover
              },
            }}
          >
            Continuar
          </Button>
  
          <Divider variant="middle" />
          <Button onClick={handleGoogleLogin} variant="contained" color="secondary" fullWidth sx={{ mt: 2 }}>
            Registrarse con Google
          </Button>
          <Typography variant="body2" align="center" sx={{ m: 2 }}>
            ¿Necesitas ayuda? <Link to="/forgotpassword">¿Olvidaste tu contraseña?</Link>
          </Typography>
          <Typography variant="body2" align="center" sx={{ mb: 2 }}>
            ¿Eres nuevo en Aguizon? <Link to="/register">Crea tu cuenta de Aguizon</Link>
          </Typography>
        </form>
        {message && (
          <Typography color="error" align="center" sx={{ mt: 2 }}>
            {message}
          </Typography>
        )}
      </Paper>
    </Container>
  );
  
};

export default LoginPage;
