import React, { useState, useEffect,useContext } from 'react';
import {jwtDecode} from 'jwt-decode';
import Navbar from '../components/navbar';
import { Container, TextField, Button, Typography, Box, IconButton, InputAdornment } from '@mui/material';
import { Visibility, VisibilityOff } from '@mui/icons-material';
import SecondaryNavbar from '../components/secondaynavbar';
import { AuthContext } from '../AuthContext';


const ProfilePage = () => {
  const { token, authProvider } = useContext(AuthContext);
  const [userData, setUserData] = useState({
    username: '',
    email: '',
  });
  const [isLoading, setIsLoading] = useState(true);
  const [showPassword, setShowPassword] = useState(false);
  const [isChangingPassword, setIsChangingPassword] = useState(false);
  const [passwords, setPasswords] = useState({
    currentPassword: '',
    newPassword: '',
    confirmNewPassword: ''
  });
  const [passwordError, setPasswordError] = useState('');
  const [newEmail, setNewEmail] = useState(''); // Estado para almacenar el nuevo email 
  const [isEmailEdited, setIsEmailEdited] = useState(false)
  const [newUsername, setNewUsername] = useState('');
  const [isUsernameEdited, setIsUsernameEdited] = useState(false)

 

  useEffect(() => {
    const fetchProfileData = async () => {
      try {
        const token = localStorage.getItem('token');
        const decodedToken = jwtDecode(token);
        const userId = decodedToken.id;

        const response = await fetch(`http://localhost:5000/api/users/profile/${userId}`);
        if (response.ok) {
          const data = await response.json();
          setUserData(data);
          setNewEmail(data.email); // Establecer el email actual en el estado `newEmail`
          setNewUsername(data.username);
          setIsLoading(false);
        } else {
          console.error('Error al recuperar datos del perfil');
        }
      } catch (error) {
        console.error('Error:', error);
      }
    };

    fetchProfileData();
  }, []);

  const handleChangePassword = (e) => {
    const { name, value } = e.target;
    setPasswords((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleChangeEmail = (e) => {
    setNewEmail(e.target.value); 
    setIsEmailEdited(true);
  };

  const handleChangeUsername = (e) => {
    setNewUsername(e.target.value);
    setIsUsernameEdited(true);
  };

  const handleTogglePasswordVisibility = () => {
    setShowPassword(!showPassword);
  };

  const handleSubmitChangePassword = async (e) => {
    e.preventDefault();
    if (passwords.newPassword !== passwords.confirmNewPassword) {
      alert('Las nuevas contraseñas no coinciden');
      return;
    }
    if (!isValidPassword(passwords.newPassword)) {
      alert('La nueva contraseña no cumple con los requisitos.');
      return;
    }

    try {
      const token = localStorage.getItem('token');
      const decodedToken = jwtDecode(token);
      const userId = decodedToken.id;

      const response = await fetch(`http://localhost:5000/api/users/change-password`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          userId,
          currentPassword: passwords.currentPassword,
          newPassword: passwords.newPassword,
        }),
      });

      if (response.ok) {
        alert('Contraseña cambiada exitosamente');
        setIsChangingPassword(false);
        setPasswords({
          currentPassword: '',
          newPassword: '',
          confirmNewPassword: ''
        });
      } else {
        alert('Error al cambiar la contraseña');
      }
    } catch (error) {
      console.error('Error:', error);
    }
  };

  const handleSubmitChangeEmail = async () => {
    try {
      const token = localStorage.getItem('token');
      const decodedToken = jwtDecode(token);
      const userId = decodedToken.id;

      const response = await fetch(`http://localhost:5000/api/users/change-email`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          userId,
          email: userData.email, // Utiliza el email actual del usuario
          newEmail,
        }),
      });

      if (response.ok) {
        alert('Correo electrónico cambiado exitosamente');
        setIsEmailEdited(false); // Reinicia el estado de edición de email
        setUserData(prevUserData => ({
          ...prevUserData,
          email: newEmail // Actualiza el email en el estado local
        }));
      } else {
        alert('Error al cambiar el correo electrónico');
      }
    } catch (error) {
      console.error('Error:', error);
    }
  };
  

  const handleSubmitChangeUsername = async () => {
    try {
      const token = localStorage.getItem('token');
      const decodedToken = jwtDecode(token);
      const userId = decodedToken.id;
  
      const response = await fetch(`http://localhost:5000/api/users/change-username`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          userId,
          username: newUsername,
        }),
      });
  
      if (response.ok) {
        const data = await response.json();
        alert('Nombre de usuario cambiado exitosamente');
        setIsUsernameEdited(false); // Reinicia el estado de edición de email
        setNewUsername('');
      } else {
        const errorData = await response.json();
        alert(`Error al cambiar el nombre de usuario: ${errorData.message}`);
      }
    } catch (error) {
      console.error('Error:', error);
      alert('Error al cambiar el nombre de usuario');
    }
  };
  

  const isValidPassword = (password) => {
    const regex = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/;
    return regex.test(password);
  };

  return (
    <div>
      <Navbar />
      <SecondaryNavbar />
      <Container maxWidth="sm" sx={{ mt: 4 }}>
        {isLoading ? (
          <Typography variant="h6">Cargando datos del perfil...</Typography>
        ) : (
          <Box>
            <Typography variant="h4" align="center" gutterBottom>
              Perfil de Usuario
            </Typography>
            <Box
              component="form"
              sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}
              noValidate
              autoComplete="off"
            >
              <TextField
                label="Nombre"
                variant="outlined"
                value={newUsername}
                onChange={handleChangeUsername}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <Button
                        variant="contained"
                        color="primary"
                        onClick={handleSubmitChangeUsername}
                        disabled={!isUsernameEdited} // Deshabilita el botón si el email no ha sido editado
                      >
                        Guardar
                      </Button>
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                label="Correo Electrónico"
                variant="outlined"
                value={newEmail}
                onChange={handleChangeEmail}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <Button
                        variant="contained"
                        color="primary"
                        onClick={handleSubmitChangeEmail}
                        disabled={!isEmailEdited} // Deshabilita el botón si el email no ha sido editado
                      >
                        Guardar
                      </Button>
                    </InputAdornment>
                  ),
                }}
              />
              {authProvider !== 'google' && (
                                <>
              {isChangingPassword && (
                <>
                  <TextField
                    label="Contraseña Actual"
                    variant="outlined"
                    type={showPassword ? 'text' : 'password'}
                    name="currentPassword"
                    value={passwords.currentPassword}
                    onChange={handleChangePassword}
                    InputProps={{
                      endAdornment: (
                        <InputAdornment position="end">
                          <IconButton
                            aria-label="toggle password visibility"
                            onClick={handleTogglePasswordVisibility}
                            edge="end"
                          >
                            {showPassword ? <VisibilityOff /> : <Visibility />}
                          </IconButton>
                        </InputAdornment>
                      ),
                    }}
                  />
                  <TextField
                    label="Nueva Contraseña"
                    variant="outlined"
                    type={showPassword ? 'text' : 'password'}
                    name="newPassword"
                    value={passwords.newPassword}
                    onChange={handleChangePassword}
                    error={!isValidPassword(passwords.newPassword)}
                    helperText={!isValidPassword(passwords.newPassword) && 'La contraseña debe contener al menos 8 caracteres, una letra mayúscula, un número y un carácter especial.'}
                    InputProps={{
                      endAdornment: (
                        <InputAdornment position="end">
                          <IconButton
                            aria-label="toggle password visibility"
                            onClick={handleTogglePasswordVisibility}
                            edge="end"
                          >
                            {showPassword ? <VisibilityOff /> : <Visibility />}
                          </IconButton>
                        </InputAdornment>
                      ),
                    }}
                  />
                  <TextField
                    label="Confirmar Nueva Contraseña"
                    variant="outlined"
                    type={showPassword ? 'text' : 'password'}
                    name="confirmNewPassword"
                    value={passwords.confirmNewPassword}
                    onChange={handleChangePassword}
                    error={passwords.newPassword !== passwords.confirmNewPassword}
                    helperText={passwords.newPassword !== passwords.confirmNewPassword && 'Las contraseñas no coinciden'}
                    InputProps={{
                      endAdornment: (
                        <InputAdornment position="end">
                          <IconButton
                            aria-label="toggle password visibility"
                            onClick={handleTogglePasswordVisibility}
                            edge="end"
                          >
                            {showPassword ? <VisibilityOff /> : <Visibility />}
                          </IconButton>
                        </InputAdornment>
                      ),
                    }}
                  />
                  <Button variant="contained" color="primary" onClick={handleSubmitChangePassword}>
                    Cambiar Contraseña
                  </Button>
                </>
              )}
              {!isChangingPassword && (
                <Button variant="contained" color="secondary" onClick={() => setIsChangingPassword(true)}>
                  Cambiar Contraseña
                </Button>
             )}
             </>
           )}
         </Box>
       </Box>
     )}
   </Container>
 </div>
);
};

export default ProfilePage;
